import { configureStore } from '@reduxjs/toolkit';

import {
  persistStore,
  FLUSH,
  REHYDRATE,
  PAUSE,
  PERSIST,
  PURGE,
  REGISTER,
} from 'redux-persist';
import { transactionReducer } from './transactions/transactionSlice';
import { modalReducer } from './modal/ModalSlice';
import { PersistedCurrencyReducer } from './currencyReducer/currencySlice';
// import { categoryReducer } from './categorySlice';

// //!    ,      persist,        .
// const authPersistConfig = {
//   key: 'user',
//   storage,
//   whitelist: ['token'],
// };
//         , export const persistedAuthReducer = persistReducer(persistConfig, < >);
export const store = configureStore({
  reducer: {
    // auth:  ,  persistReducer(authPersistConfig, < >)  persistedAuthReducer     
    transactions: transactionReducer,
    modal: modalReducer,
    currency: PersistedCurrencyReducer,
  },
  middleware: getDefaultMiddleware =>
    getDefaultMiddleware({
      serializableCheck: {
        ignoredActions: [FLUSH, REHYDRATE, PAUSE, PERSIST, PURGE, REGISTER],
      },
    }),
});
export const peristor = persistStore(store);
